.form__wrapper {
  width: auto;
  padding-bottom: 26px;

  @media (min-width: @tablet-width) {
    width: 708px;
    padding-right: 37px;
    padding-bottom: 95px;
    padding-left: 40px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    width: 1220px;
    padding: 0;
    padding-bottom: 89px;
  }
}

.form__fieldset-data {
  .fieldset ();

  padding-bottom: 24px;

  @media (min-width: @tablet-width) {
    padding-bottom: 72px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 0;
  }
}

.form__fieldset-data-list {
  .fieldset-list ();

  display: grid;
  grid-template-rows: 1fr;
  row-gap: 13px;

  color: @special_dark-grey;

  @media (min-width: @tablet-width) {
    row-gap: 20px;
    width: auto;
  }
}

.form__fieldset-data-item {
  display: flex;
  align-items: center;
}

.form__fieldset-data-name {
  .fieldset-input ();

  margin-left: 14px;
  padding-right: 15px;
  padding-left: 15px;

  @media (min-width: @tablet-width) {
    padding-right: 19px;
    padding-left: 19px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.form__fieldset-data-weight {
  .fieldset-input ();

  margin-left: 14px;
  padding-right: 16px;
  padding-left: 16px;

  @media (min-width: @tablet-width) {
    padding-right: 19px;
    padding-left: 19px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 19px;
    padding-left: 19px;
  }
}

.form__fieldset-data-age {
  .fieldset-input ();

  margin-left: 14px;
  padding-right: 16px;
  padding-left: 16px;

  @media (min-width: @tablet-width) {
    padding-right: 20px;
    padding-left: 20px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 18px;
    padding-left: 18px;
  }
}

.form__fieldset-program {
  .fieldset ();

  padding-top: 20px;
  padding-bottom: 22px;

  border-top: 1px solid @separate_line;
  border-bottom: 1px solid @separate_line;
  //outline: 5px solid rgba(0, 0, 255, 0.5);
  //outline-offset: -5px;

  @media (min-width: @tablet-width) {
    padding-top: 25px;
    padding-right: 28px;
    padding-bottom: 25px;
    padding-left: 28px;

    border: 1px solid @separate_line-tablet;
  }
}

.form__fieldset-program-list {
  .fieldset-list ();

  display: grid;
  grid-template-rows: 1fr;
  row-gap: 13px;

  color: @special_dark-grey-2;

  @media (min-width: @tablet-width) {
    width: auto;
  }
}

.form__fieldset-program-label {
  .fieldset-input-decoration ();

  &::before {
    border-radius: 50%;
  }
}

.form__fieldset-program-input:checked + .form__fieldset-program-label {
  &::after {
    content: "";
    position: absolute;
    left: ((@radio-check-size - @radiobutton-size-active) / 2);

    width: @radiobutton-size-active;
    height: @radiobutton-size-active;

    background-color: @basic_green;
    border-radius: 50%;
  }

  @media (min-width: @tablet-width) {
    &::after {
      left: (((@radio-check-size + 10) - (@radiobutton-size-active + 4.5)) / 2);

      width: (@radiobutton-size-active + 4.5);
      height: (@radiobutton-size-active + 4.5);
    }
  }
}

.form__fieldset-contact-details {
  .fieldset ();

  position: relative;

  padding-top: 66px;
  padding-bottom: 44px;

  border-top: 2px solid @basic_green;
  //outline: 5px solid rgba(0, 255, 0, 0.5);
  //outline-offset: -5px;

  @media (min-width: @tablet-width) {
    padding-top: 88px;
    padding-bottom: 89px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 93px;
  }
}

.form__fieldset-contact-details-title {
  .fieldset-title ();

  &--line {
    position: absolute;
    top: -18px;

    display: flex;
    justify-content: center;
    width: 100%;
    margin-left: -49px;

    @media (min-width: @tablet-width) {
      top: -24px;

      margin-left: -57px;
    }

    @media (min-width: @desktop-width) {
      margin-left: -354px;
    }
  }

  @media (min-width: @tablet-width) {
    //margin-bottom: 76px;
    //padding-top: 75px;
  }

  @media (min-width: @desktop-width) {
    //padding-top: 69px;
  }
}

.form__fieldset-contact-details-title-text {
  display: block;
  width: 222px;
  padding-right: 15px;
  padding-left: 20px;

  background-color: @basic-white;

  @media (min-width: @tablet-width) {
    width: 593px;
    padding-right: 39px;
    padding-left: 39px;
  }

  @media (min-width: @desktop-width) {
    width: 592px;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.form__fieldset-contact-details-list {
  .fieldset-list ();

  display: grid;
  grid-template-rows: 1fr;
  row-gap: 14px;

  color: @special_dark-grey;

  @media (min-width: @tablet-width) {
    row-gap: 20px;
    padding-right: 37px;
    padding-left: 40px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    padding: 0;
  }
}

.form__fieldset-contact-details-item {
  display: flex;
  align-items: center;
}

