  &--line {
    position: absolute;
    top: -17px;

    display: flex;
    justify-content: center;
    width: 100%;
    margin-left: -85px;

    @media (min-width: @tablet-width) {
      top: -24px;

      margin-left: -224px;
    }

    @media (min-width: @desktop-width) {
      margin-left: -519px;
    }
  }
}

.form__fieldset-comment-title-text {
  display: block;
  padding-right: 15px;
  padding-left: 20px;

  background-color: @basic-white;

  @media (min-width: @tablet-width) {
    padding-right: 38px;
    padding-left: 38px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 40px;
    padding-left: 40px;
  }
}

.form__fieldset-comment-textarea {
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    padding-right: 37px;
    padding-left: 40px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.form__fieldset-comment-text {
  display: block;
  padding: 0;
  padding-top: 13px;
  padding-right: 15px;
  padding-bottom: 13px;
  padding-left: 12px;

  font-size: @size;
  line-height: 1.12;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: @special_dark-grey;

  border: 1px solid;

  @media (min-width: @tablet-width) {
    padding-top: 10px;
    padding-right: 19px;
    padding-bottom: 10px;
    padding-left: 19px;

    font-size: (@size + 4);
    line-height: 1.5;
  }
}

