.location {
  outline: 5px solid rgba(0, 0, 255, 0.5);
  outline-offset: -5px;

  &--background {
    background-color: @basic-white;
  }

  @media (min-width: @desktop-width) {
    position: relative;

    width: 1440px;
    margin: 0 auto;
  }
}

.location__wrapper {
  display: flex;
  justify-content: space-between;

  padding-top: 28px;
  padding-bottom: 22px;

  @media (min-width: @tablet-width) {
    padding-top: 62px;
    padding-right: 41px;
    padding-bottom: 51px;
    padding-left: 41px;
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    top: 95px;
    left: 110px;

    width: 570px;
    padding-right: 89px;
    padding-left: 76px;

    background-color: @basic-white;
  }
}

.location__invitation {
  margin: 0;

  font-size: @size;
  line-height: 1.12;
  text-transform: uppercase;
  color: @cat_energy;

  @media (min-width: @tablet-width) {
    width: 171px;
    font-size: (@size + 4);
    line-height: 1.05;
  }

  @media (min-width: @desktop-width) {
    width: 170px;
  }
}

.location__adress {
  margin: 0;

  font-size: (@size - 2);
  line-height: 1.43;
  font-family: "Arial", sans-serif;
  color: @special_dark-grey;

  @media (min-width: @tablet-width) {
    width: 159px;
    font-size: @size;
    line-height: 1.25;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    width: 170px;

    text-align: left;
  }
}

.location__map {
  margin: 0;

  text-align: center;

  background-color: @basic_grey;
}

.location__map img {
  @media (min-width: @tablet-width) {
    width: 768px;
    height: 400px;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    height: 400px;
  }
}
